	module.exports = {"data":{"markdownRemark":{"html":"<h3>Inline Image and Attachments on the Security Page</h3>\n<p>We now enable you to attach pictures and other files to your policy. Simply go to your program's <b>Settings > Policy</b> and there will be a field where you can upload your files. We've got a nice giph on ours. <a href=\"https://hackerone.com/security\">Check it out</a>.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/nov_2017-e5f8bc1b9023e09bc8da463a7e87d5e2-8c92c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 52.307692307692314%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"nov_2017\"\n        title=\"\"\n        src=\"/static/nov_2017-e5f8bc1b9023e09bc8da463a7e87d5e2-e7ffa.png\"\n        srcset=\"/static/nov_2017-e5f8bc1b9023e09bc8da463a7e87d5e2-49813.png 125w,\n/static/nov_2017-e5f8bc1b9023e09bc8da463a7e87d5e2-dc8a5.png 250w,\n/static/nov_2017-e5f8bc1b9023e09bc8da463a7e87d5e2-e7ffa.png 500w,\n/static/nov_2017-e5f8bc1b9023e09bc8da463a7e87d5e2-dda24.png 750w,\n/static/nov_2017-e5f8bc1b9023e09bc8da463a7e87d5e2-8c92c.png 780w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/changelog/2017/november","title":"November 2017"}}},"pathContext":{}}

